import React, { PureComponent } from "react";
import MobileClient from "../MobileClient/MobileClient";
import EventEmitter from "../../utils/EventEmitter";
import "./Mobile.css";

class Mobile extends PureComponent {
  constructor(props) {
    super(props);
    console.log("Mobile render");

    this.events = new EventEmitter();
    this.state = {
      clients: this.props.clients.map((client) => ({
        ...client,
        balance: client.balance || 0,
        id: Math.random().toString(36).substr(2, 9),
      })),
      filter: "all", // all, active, blocked
    };

    this.setupEventListeners();
  }

  setupEventListeners = () => {
    this.events.on("clientUpdated", this.handleClientUpdate);
    this.events.on("clientDeleted", this.handleClientDelete);
  };

  handleClientUpdate = (oldClient, newClient) => {
    this.setState((prevState) => {
      const clients = prevState.clients.map((client) =>
        client === oldClient ? { ...newClient, id: client.id } : client
      );
      return { clients };
    });
  };

  handleClientDelete = (clientToDelete) => {
    this.setState((prevState) => ({
      clients: prevState.clients.filter((client) => client !== clientToDelete),
    }));
  };

  handleAddClient = () => {
    const newClient = {
      id: Math.random().toString(36).substr(2, 9),
      surname: "",
      name: "",
      patronymic: "",
      balance: 0,
      status: "active",
    };

    this.setState((prevState) => ({
      clients: [...prevState.clients, newClient],
    }));
  };

  handleFilterChange = (filter) => {
    this.setState({ filter });
  };

  getFilteredClients = () => {
    const { clients, filter } = this.state;

    switch (filter) {
      case "active":
        return clients.filter((client) => client.status === "active");
      case "blocked":
        return clients.filter((client) => client.status === "blocked");
      default:
        return clients;
    }
  };

  render() {
    console.log("Mobile render called");
    const { filter } = this.state;
    const filteredClients = this.getFilteredClients();

    return (
      <div className="mobile-container">
        <h1> </h1>

        <div className="controls">
          <button onClick={this.handleAddClient} className="btn-add">
            +  
          </button>

          <div className="filters">
            <button
              onClick={() => this.handleFilterChange("all")}
              className={filter === "all" ? "active" : ""}
            >
              
            </button>
            <button
              onClick={() => this.handleFilterChange("active")}
              className={filter === "active" ? "active" : ""}
            >
              
            </button>
            <button
              onClick={() => this.handleFilterChange("blocked")}
              className={filter === "blocked" ? "active" : ""}
            >
              
            </button>
          </div>
        </div>

        <table className="clients-table">
          <thead>
            <tr>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {filteredClients.map((client) => (
              <MobileClient
                key={client.id}
                client={client}
                events={this.events}
              />
            ))}
          </tbody>
        </table>

        <div className="stats">
          : {this.state.clients.length} | :{" "}
          {this.state.clients.filter((c) => c.status === "active").length} |
          :{" "}
          {this.state.clients.filter((c) => c.status === "blocked").length}
        </div>
      </div>
    );
  }
}

export default Mobile;



